<template>
<div>
    <br>


    <div class="container-fluid mt-4">
    <b-alert :show="loading" variant="info">Cargando...</b-alert>
    <b-alert :show="messageError" variant="danger">{{message}}</b-alert>
    <b-alert :show="messageSuccess" variant="success">{{message}}</b-alert>

    <b-row>
        <!-- <b-col sm="4"><p>ltima temperatura medida {{lastTemperature}} <bar-chart-2-icon class="custom-class"></bar-chart-2-icon>  </p></b-col>
    --></b-row>
    <br>

        <b-row class="bordergreen ">
          <b-col md="12" style="padding-bottom:2%">Gestionar riegos</b-col><br>
              <b-col md="6">
                    <b-row>
                       <multiselect v-model="selectedPlants" :options="options" :multiple="true" group-values="names" group-label="plants" :group-select="true" placeholder="Busca tu planta " track-by="name" label="name"><span slot="noResult">Oops! no hay plantas.</span></multiselect>
                    </b-row>
              </b-col>
              <b-col md="3">
                     <b-form-checkbox id="checkbox2" v-model="medition" value="1" unchecked-value="-1">Solo medicin
                      </b-form-checkbox>
              </b-col>
              <b-col md="3">
                  <span class="btn" v-on:click="sendOperations()"><check-icon class="custom-class"></check-icon>Realizar</span>

              </b-col>
              <br><br>

        </b-row>
      <br>
      <b-row>

        <!-- <b-tabs>
          <b-tab title="Riegos de hoy " active>


                  <br>
                  <table class="table table-striped">
