                      <tr>
                        <th v-if="false" >Identificador</th>

                        <th>Nombre de planta</th>
                        <th>Cantidad de agua aplicada </th>
                        <th>evapotranspiracion </th>
                        <th>Humedad medida En riego</th>
                        <th>Temperatura en riego </th>

                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="registro in riegos" :key="registro.id">
                        <td v-if="false">{{ registro.id }}</td>
                        <td>{{ registro.nomPlanta }}</td>
                        <td>{{ registro.cantAplicada }}</td>
                        <td>{{ registro.evapotranspiracion }}</td>
                        <td>{{ registro.humedadMedida }}</td>
                        <td>{{ registro.temperaturaMedida }}</td>
                      </tr>
                    </tbody>
                  </table>


          </b-tab>
          <b-tab title="Humedad de hoy" >
          <br>
                    <table class="table table-striped">
                      <thead>
                        <tr>
                          <th v-if="false" >Identificador</th>
                          <th>Nombre de planta </th>
                          <th>Humedad</th>
                          <th>&nbsp;</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="medition in humedity" :key="medition.id">
                          <td v-if="false">{{ medition.id }}</td>
                          <td>{{ medition.nomPlanta }}</td>
                          <td>{{ medition.humedad }}</td>
                        </tr>
                      </tbody>
                    </table>

          </b-tab>
        </b-tabs>
 -->

    </b-row>
    <br>
  </div>

  </div>
</template>

<script>
import api from '@/api'
import Multiselect from 'vue-multiselect'
import { CheckIcon,BarChart2Icon } from 'vue-feather-icons'
import auth from '../../auth'
import yyyymmdd from   'yyyy-mm-dd'
import { log } from 'util';

export default {
  data () {
    return {
      loading: false,
      messageError:false,
      messageSuccess:false,
      message:'',
      model: {},
      riegos:[],
      plantas : [],
      lastTemperature:"",
      humedity:[],
      medition:'-1',
      options: [{ plants: 'Todas las plantas',names: []}],
      selectedPlants: [],
      agricultorId:''
    }
  },
  components: {
    Multiselect,CheckIcon,BarChart2Icon
  },
  async created () {
    this.refresh()
  },
  methods: {

    async refresh () {
      this.selectedPlants = [] ;
      this.plantas = [] ;
      this.options[0].names = [];
      this.loading = true

      var date = new Date();
      let fechaStart = yyyymmdd(new Date(date.getFullYear(), date.getMonth(), date.getDate())) + "T00:00:00.000Z";
      let fechaEnd = yyyymmdd(new Date(date.getFullYear(), date.getMonth(), date.getDate())) + "T23:59:59.999Z";

      this.agricultorId = auth.getAgricultor().agricultorId;

      var uri = auth.api_url + 'riegos?filter={"where":{"and":[{"agricultorId": "'+this.agricultorId+'" },{"fecha": { "gte":  '+Date.parse(fechaStart)+', "lte":  '+Date.parse(fechaEnd)+'}}]} }';


      this.riegos = await api.getWithCondition(this,uri);
      this.plantas = await api.getAgricultorRelation(this,'planta');

      let uriH = auth.api_url + 'medicionesHumedad?filter={"where":{"and":[{"agricultorId": "'+this.agricultorId+'" },{"fecha": { "gte":  '+Date.parse(fechaStart)+', "lte":  '+Date.parse(fechaEnd)+'}}]} }';
      this.humedity = await api.getWithCondition(this,uriH);
      console.log(uriH);


      let uriT = auth.api_url + 'mediciones?filter={"where":{"and":[{"agricultorId": "'+this.agricultorId+'" }]},"order":["fecha desc"],"limit":1 }';
      this.lastTemperature = await api.getWithCondition(this,uriT);
      this.lastTemperature = this.lastTemperature[0].temperaturaAmbiente;

      this.selectedPlants = [] ;

      if(this.plantas === null  ||  this.plantas.length == 0 )
      {
          this.$refs.modalMsg.show();
          var v = this;
          setTimeout(()=>{ v.$router.push('/site/plants');}, 3000);
      }

      this.plantas.forEach(element => {
        var option = {name:element.alias,id:element.id} ;
        this.options[0].names.push(option);
      });

      this.loading = false
    },
    async sendOperations () {

      if(this.selectedPlants.length==0) {
        this.messageError = true;
        this.message = "Agrega plantas!";
        return;
      }
      this.messageError = false;
      let data = [];
      console.log(this.medition);

      let riego = (this.medition=='-1') ? 1 : 0  ;
      this.loading = true
      this.selectedPlants.forEach(element =>
      {
        var plant = this.plantas.filter(function(v) {return v.id === element.id; })[0];
        if(!plant) return ;

        var date = new Date();
        let x = (this.medition=='-1') ? plant.posRiego.x : plant.posHumedad.x  ;
        let y = (this.medition=='-1') ? plant.posRiego.y : plant.posHumedad.y ;

        var operation = {
           agricultorId:this.agricultorId,
                          prototipoId:"string",
                          tipo:"medicion_humedad",
                          datos:{
                                posPlantax : x,
                                posPlantay :y,
                                planta   : plant.id,
                                hora : date.getHours(),
                                riego
                          }
         };

        data.push(operation);
      });
      await api.create(this,data,true,'operacionesPrototipo');
      this.messageSuccess = true;
      this.message = "Se ha gestionado la solicitud!!";
      setTimeout(function () { this.messageSuccess = false;}.bind(this), 4000)
      this.loading = false;
      this.refresh();
    },
  }
}
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style >
.bordergreen {
    border: solid 1px #51C85D;
    padding: 2%;
    border-radius: 5px;
}
</style>



