<template>
    <div>    <br>
    <div class="container mt-4">
    <b-alert :show="loading" variant="info">Cargando...</b-alert>
    <b-row>
      <b-col sm="8">
        <table class="table table-striped">
          <thead>
            <tr>
              <th v-if="false">Identificador</th>
              <th>Planta</th>
              <th>Hora riego</th>
              <th>Regar?</th>
              <th>&nbsp;</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="registro in registros" :key="registro.id">
              <td v-if="false">{{ registro.id }}</td>
              <td>{{ registro.planta }}</td>
              <td>{{ registro.horaRiego }}</td>
              <td>{{ registro.regar }}</td>

              <td class="text-right">
                <a href="#" @click.prevent="populateModelToEdit(registro)"><edit-icon class="custom-class"></edit-icon></a> 
              </td>
              <td>
                <a href="#" @click.prevent="deleteModel(registro.id)"> <delete-icon class="custom-class"></delete-icon> </a>
              </td>
            </tr>

            
          </tbody>
        </table>
      </b-col>
     <b-col sm="4">
        <b-card :title="(model.id ? 'Editar ID#' + model.id : 'Agregar')">
          <form @submit.prevent="saveModel">
            <b-form-group label="Planta">

              <b-form-select  v-model="model.plantaId" >
                <option :value="null" disabled>-- Selecciona una planta --</option>
                <option  v-for="planta in plantas"  :key="planta.id" :value="planta.id">{{planta.alias}}</option>
               </b-form-select>
              
            </b-form-group>
            <b-form-group label="Hora Riego">
                <b-form-select v-model="model.horaRiego" :options="optionsHour" class="mb-3">
                  <template slot="first">
                      <option :value="null" disabled>-- Selecciona una hora --</option>
                  </template>
              </b-form-select>
            </b-form-group>
            <b-form-group label="">
                  <b-form-checkbox 
                     v-model="model.riego"
                     :checked="true"
                     value= true
                     unchecked-value= false
                     >
                 Regar? 
              </b-form-checkbox>
              
            </b-form-group>
            
            <div>
              <b-btn type="submit" variant="success">Guardar</b-btn>
            </div>
          </form>
        </b-card>
      </b-col>
       
    </b-row>
  </div>

  <b-modal ref="modalMsg" hide-footer title="No puedes usar este componente">
    No tienes plantas credas .... 
  </b-modal>
  </div>



</template>

<script>
import api from '@/api'
import { EditIcon,DeleteIcon } from 'vue-feather-icons'
export default {
  data () {
    return {
      loading: false,
      registros: [],
      plantas : [],
      model: {},
      optionsHour: [
        { value: '00', text: '00' },
        { value: '01', text: '01' },
        { value: '02', text: '02' },
        { value: '03', text: '03' },
        { value: '04', text: '04' },
        { value: '05', text: '05' },
        { value: '06', text: '06' },
        { value: '07', text: '07' },
        { value: '08', text: '08' },
        { value: '09', text: '09' },
        { value: '10', text: '10' },
        { value: '11', text: '11' },
        { value: '12', text: '12' },
        { value: '14', text: '14' },
        { value: '15', text: '15' },
        { value: '16', text: '16' },
        { value: '17', text: '17' },
        { value: '18', text: '18' },
        { value: '19', text: '19' },
        { value: '20', text: '20' },
        { value: '21', text: '21' },
        { value: '22', text: '22' },
        { value: '23', text: '23' }
      ]
    }
  },
  components: {
    EditIcon,DeleteIcon
  },
  async created () {
    this.refresh()
  },
  methods: {
    async refresh () {
      
      this.loading = true
      this.registros = await api.get(this,true,'agendaPlanta')
      this.plantas = await api.getAgricultorRelation(this,'planta')
      if(this.plantas === null  ||  this.plantas.length == 0 )
      { 
          this.$refs.modalMsg.show();
          var v = this;
          setTimeout(()=>{ v.$router.push('/site/plants');}, 3000);
      }
      this.loading = false
      //change name of plant
      this.registros.forEach(element => {
      var planta = this.plantas.filter(function(item) { return item.id === element.plantaId; }); 
        element.planta = planta[0].alias;
      });
    },
    async populateModelToEdit (registro) {
      this.model = Object.assign({}, registro)
      this.model.riego = 'true';
    },
    async saveModel () {
      
      if (this.model.id) {
        await api.update(this,this.model.id, this.model,'agendaPlanta')
      } else {
        console.log(this.model);
        
        await api.create(this,this.model,true,'agendaPlanta')
      }
      this.model = {} // reset form
      await this.refresh()
    },
    async deleteModel (id) {
      if (confirm('Estas seguro de eliminar este registro?')) {
        if (this.model.id === id) {
          this.model = {}
        }
        await api.delete(this,id,'agendaPlanta')
        await this.refresh()
      }
    }
  }
}
</script>

<style >
</style>


