<template>
   <div>
     <div class="container-fluid mt-4">
     <b-alert :show="loading" variant="info">Cargando...</b-alert>
      </div>
   <br><br><br>
   <b-row>
     <b-tabs>
       <b-tab title="Riegos en plantas" active>
          <br><br>
             <b-row>
               <b-col offset-lg="1"  lg="10" >
                 <mdb-datatable
                   :data="dataIrriwation"
                   striped
                   bordered
                 />
               </b-col>

             </b-row>

       </b-tab>
       <b-tab title="Mediciones de humedad" >
       <br><br>
       <b-row>
         <b-col offset-lg="1"  lg="10" >
           <mdb-datatable
             :data="dataHumedity"
             striped
             bordered
           />
         </b-col>

       </b-row>

       </b-tab>
     </b-tabs>


 </b-row>

 </div>
</template>

<script>
import api from '@/api'
import Chart from 'chart.js';
import auth from '../../auth'
import yyyymmdd from   'yyyy-mm-dd'
import axios from 'axios'
import { mdbDatatable } from 'mdbvue';

    export default
    {
        name: 'app',
        components: {
          mdbDatatable
        },
        data() {
          return {
            dataIrriwation: {
              columns: [
                {
                  label: 'Nombre de planta',
                  field: 'nomPlanta',
                  sort: 'asc'
                },
                {
                  label: 'Cantidad aplicada',
                  field: 'cantAplicada',
                  sort: 'asc'
                },
                {
                  label: 'Evapotranspiracion',
                  field: 'evapotranspiracion',
                  sort: 'asc'
                },
                {
                  label: 'Humedad en riego',
                  field: 'humedadMedida',
                  sort: 'asc'
                },
                {
                  label: 'Temperatura de riego',
                  field: 'temperaturaMedida',
                  sort: 'asc'
                },
                {
                  label: 'Fecha',
                  field: 'fecha',
                  sort: 'asc'
                }
              ],
              rows: [

              ]
            },
            dataHumedity: {
              columns: [
                {
                  label: 'Id planta',
                  field: 'plantaId',
                  sort: 'asc'
                },
                {
                  label: 'Humedad',
                  field: 'humedad',
                  sort: 'asc'
                },
                {
                  label: 'Fecha',
                  field: 'fecha',
                  sort: 'asc'
                },
              ],
              rows: [

              ]
            },
            loading:false,
          }
        },async created () {
          this.refresh()
        },methods: {

          async refresh () {
            this.loading = true
            let irriwations = await api.getAgricultorRelation(this,'riegos');
            if (irriwations) {
              irriwations.forEach(irriwation =>
              {
                        this.dataIrriwation.rows.push({
                          nomPlanta:irriwation.nomPlanta,
                          cantAplicada:String(irriwation.cantAplicada),
                          evapotranspiracion:String(irriwation.evapotranspiracion),
                          humedadMedida:String(irriwation.humedadMedida),
                          temperaturaMedida:String(irriwation.temperaturaMedida),
                          fecha:irriwation.fecha
                        });
              });
            }


            let humedity = await api.getAgricultorRelation(this,'medicionHumedad');
            if (humedity) {

              humedity.forEach(h =>
              {
                        this.dataHumedity.rows.push({
                          plantaId:h.plantaId,
                          humedad:String(h.humedad),
                          fecha:h.fecha
                        });
              });

            }
            this.loading = false
          }
        },
    }
</script>



