import Vue from 'vue'
import Router from 'vue-router'
// vistas sin registro
import NotFoundView from '@/components/404.vue'
import Home from '@/components/Home'
import Login from '@/components/Login'
import Registro from '@/components/Registro'
// layout main
import DashView from '@/components/layouts/Main'
//views
import DashboardView from '@/components/views/Dashboard.vue'
import RiegosView from '@/components/views/Riegos.vue'
import PlantasView from '@/components/views/Plantas.vue'
import ConfiguracionView from '@/components/views/Configuracion.vue'
import ReporteView from '@/components/views/Reporte.vue'
import AgendaRiegoView from '@/components/views/AgendaRiego.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
        path: '/login',
        name: 'login',
        component: Login
    },
    {
        path: '/registro',
        name: 'registro',
        component: Registro
    }
    ,{
      path: '/site',
      component: DashView,
      children: [
        {
          path: 'dashboard',
          alias: '',
          component: DashboardView,
          name: 'Dashboard',
          meta: {description: 'Overview of environment'}
        }, {
          path: 'plants',
          component: PlantasView,
          name: 'Plantas',
          meta: {description: 'Todas las plantas del sistema'}
        },
        {
          path: 'schedulePlants',
          component: AgendaRiegoView,
          name: 'AgendarRiegos',
          meta: {description: 'Programar riego de las plantas'}
        },
        {
          path: 'irriwation',
          component: RiegosView,
          name: 'Riegos',
          meta: {description: 'Riegos de plantas'}
        },
        ,
        {
          path: 'config',
          component: ConfiguracionView,
          name: 'Configuracion',
          meta: {description: 'Configuracin'}
        },
        ,
        {
          path: 'report',
          component: ReporteView,
          name: 'Reporte',
          meta: {description: 'Reporte de los riegos'}
        },
      ]
    }, {
      // not found handler
      path: '*',
      component: NotFoundView
    }
  ],linkActiveClass: "active",
  linkExactActiveClass: "active"
})



