<template>
    <div>    <br>
    <div class="container mt-4">
    <b-alert :show="loading" variant="info">Cargando...</b-alert>
    <form @submit.prevent="saveModel">
        <b-card title="Ajustes del sistema.">
    <b-row>
      <b-col offset-sm="1" sm="4">



            <b-form-group label="Nombre del cultivo">
              <b-form-input type="text" placeholder=""  v-model="model.nombreCultivo"></b-form-input>
            </b-form-group>


            <b-form-group label="Alto del cultivo">
              <b-form-input type="text" v-model="model.medidaAlto"></b-form-input>
            </b-form-group>

             <b-form-group label="Ancho del cultivo">
              <b-form-input type="text" v-model="model.anchoAlto"></b-form-input>
            </b-form-group>


            <b-form-group label="Altura maxima que puede tener las plantas (mts)">
              <b-form-input type="text" v-model="model.alturaMaximaPlan"></b-form-input>
            </b-form-group>

             <b-form-group label="Altura a nivel del mar ">
              <b-form-input type="text" v-model="model.AlturaNivelMar"></b-form-input>
            </b-form-group>

            <b-form-group label="Volumen de la boquilla de llave o dispositivo de salida agua de Riego">
              <b-form-input type="text" v-model="model.volumenObjetoSalidaRiego"></b-form-input>
            </b-form-group>

            <b-form-group label="Longitud  Greenwich del Cultivo  ">
              <b-form-input type="text" v-model="model.longitudGreenwichCultivo"></b-form-input>
            </b-form-group>





            </b-col>
           <b-col offset-sm="1" sm="4">
              <b-form-group label="Longitud  Greenwich central ">
              <b-form-input type="text" v-model="model.longitudGreenwichCentral"></b-form-input>
            </b-form-group>
              <b-form-group label="latitud del cultivo">
              <b-form-input type="text" v-model="model.latitud"></b-form-input>
              </b-form-group>

             <b-form-group label="Distancia de hilera entre plantas">
              <b-form-input type="text" v-model="model.distanciaHileraPlantas"></b-form-input>
              </b-form-group>

              <b-form-group label="Distancia Entre Plantas ">
              <b-form-input type="text" v-model="model.distanciaEntrePlantas"></b-form-input>
              </b-form-group>

              <b-form-group label="Que tanta agua necesita el cultivo?">
              <b-form-input  type="range" min="0" step="3.0" max="9" v-model="model.coeficienteCultivo"></b-form-input>
            </b-form-group>

              <b-form-group label="Que tan optimo es el riego?">
              <b-form-input  type="range" min="0" step="3.0" max="9" v-model="model.coeficienteRiego"></b-form-input>
            </b-form-group>

            <b-form-group label="Que tan frecuente llueve?">
              <b-form-input  type="range" min="0" step="3.0" max="9" v-model="model.coeficientePrecipitacion"></b-form-input>
            </b-form-group>

            <b-form-group label="Nivel de escape de agua en el terreno?">
              <b-form-input  type="range" min="0" step="3.0" max="9" v-model="model.coeficienteEscorrentia"></b-form-input>
            </b-form-group>



      </b-col>

    </b-row>
    <b-alert :show="loadingg" variant="info">Guardando...</b-alert>
          <div>
              <b-btn type="submit" variant="success">Guardar</b-btn>
            </div>
     </b-card>
         </form>
  </div>
  </div>
</template>

<script>
import api from '@/api'
import { EditIcon,DeleteIcon } from 'vue-feather-icons'
export default {
  data () {
    return {
      loading: false,
      loadingg:false,
      registros: [],
      model: {}
    }
  },
  components: {
    EditIcon,DeleteIcon
  },
  async created () {
    this.refresh()
  },
  methods: {
    async refresh () {
      this.loading = true
      this.loadingg = false
      this.registros = await api.getAgricultorRelation(this,'configuracion')
      console.log(this.registros[0]);
      if( this.registros.length > 0 )
      {
        console.log("entro")
        this.populateModelToEdit(this.registros[0])
      }
      this.loading = false
    },
    async populateModelToEdit (registro) {
      this.model = Object.assign({}, registro)
    },
    async saveModel () {
      if (this.model.id) {
        this.loadingg = true;
        await api.update(this,this.model.id, this.model,'configuraciones')
      } else {
        await api.create(this,this.model,true,'configuraciones')
      }
      this.model = {} // reset form
      await this.refresh()
    }
  }
}
</script>

<style >
</style>



