import Vue from 'vue'
import axios from 'axios'
import auth from './auth'

const client = axios.create({
  baseURL: auth.api_url,
  json: true
})

export default {
  async execute (contexto,method, resource, data,filter,totalResource=false) {
    let accessToken = auth.getToken(contexto);
    let url = "";
    if (!totalResource) {
      url =  resource + '?access_token='+accessToken;
      if (filter)
       url = resource + '?'+filter+'&access_token='+accessToken;
    }else
     url = resource;
    console.log(url);
    if(accessToken){
        return client({
            method,
            url: url,
            data
          }).then(req => {return req.data})
    }
    else
    {
        contexto.$router.push('/login')
    }

  },
  get (context,filterAgricultor=false,modelurl) {
    var filter;
    if(filterAgricultor)
     filter= auth.filter_agricultor + auth.getAgricultor().agricultorId;
     console.log(auth.getAgricultor().agricultorId)
    return this.execute(context,'get', '/'+modelurl,null,filter)
  },
  getById (context,id,modelurl) {
    return this.execute(context,'get', `/${modelurl}/${id}`)
  },
  getAgricultorRelation (context,modelRelation) {
    let id = auth.getAgricultor().agricultorId;
    return this.execute(context,'get', `/agricultors/${id}/${modelRelation}`)
  },
  getWithCondition (context,conditition) {
    return this.execute(context,'get', conditition,'',false,true)
  },
  create (context,data,relationAgricultor,modelurl){
    if(relationAgricultor) data.agricultorId = auth.getAgricultor().agricultorId;
    return this.execute(context,'post', '/'+modelurl, data)
  },
  update (context,id, data,modelurl) {
    return this.execute(context,'put', `/${modelurl}/${id}`, data)
  },
  delete (context,id,modelurl) {
    return this.execute(context,'delete', `/${modelurl}/${id}`)
  }
}



